

import React from 'react';
import { Link } from 'react-router-dom';
import '../styles/Reklaam.css'

const Reklaam = () => {
  return (
    <div className="reklaam-container">
      <h2>{genereeriReklaamiPealkiri()}</h2>
      <p>{genereeriReklaamiLik()}</p>
      <Link to="/">Loe rohkem</Link>
    </div>
  );
};

const genereeriReklaamiPealkiri = () => {
  const reklaamiPealkirjad = ['Eripakkumine!', 'Piiratud aja pakkumine', 'Avasta pnevaid pakkumisi', 'ra maga maha!'];
  const juhuslikIndeks = Math.floor(Math.random() * reklaamiPealkirjad.length);
  return reklaamiPealkirjad[juhuslikIndeks];
};

const genereeriReklaamiLik = () => {
  const reklaamiLigud = [
    'Uuri meie hmmastavaid tooteid ja leia endale tiuslik.',
    'Lbimeldud hinnad kvaliteetsetel esemetel. Osta kohe!',
    'Liitu meie kogukonnaga ja koge vahet ostlemisel.',
    'Tsta oma elukvaliteeti meie eksklusiivsete pakkumistega.'
  ];
  const juhuslikIndeks = Math.floor(Math.random() * reklaamiLigud.length);
  return reklaamiLigud[juhuslikIndeks];
};

export default Reklaam;
